
<script setup>
import { 
	onMounted, 
	ref, 
	// unref, 
	// computed 
} from 'vue';
import { storeToRefs } from 'pinia'
import { useRouter } from 'vue-router';
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useSetupStore } from '@/store/setup.store';
import { useUserStore } from '@/store/user.store';
// institutions
const setupStore = useSetupStore();
const { institutions } = storeToRefs(setupStore);
const { getInstitutions, addInstitution, editInstitution, deleteInstitution } = setupStore;


// User
const userStore = useUserStore();
const { currentInstitutionId } = storeToRefs(userStore);

// Vue router
const router = useRouter();

const searchStr = ref("");



const addInstitutionPayload = ref({
	name: ''
})

onMounted(()=>{
	getInstitutions();
})

const addInstitutionBtnClick = ()=>{
	addInstitution(addInstitutionPayload);
}

const showUsers = (institutionId)=>{
	currentInstitutionId.value = institutionId;
	router.push('/admin/users');
}

</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addInstitutionModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getInstitutions">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!institutions.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No institution available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col-3 text-bold text-light p-2">
					Name
				</div>
				<div class="data-heading col-3 text-bold text-light p-2">
					Creation date
				</div>
				<div class="data-heading col-2"></div>
				<div class="data-heading col-2"></div>
				<div class="data-heading col-2"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="institution in institutions"
				:key="institution.id"
				:id="institution.id"
			>
				<div class="col-3">
					{{ institution.institution_name }}
				</div>
				<div class="col-3">
					{{ new Date(institution.creation_date).toISOString().slice(0, 19).replace('T', ' ') }}
				</div>
				<div class="col-2">
					<button class="btn btn-secondary" :id="institution.id" @click="showUsers(institution.id)">Show users</button>
				</div>
				<div class="col-2">
					<button class="btn btn-secondary" :id="institution.id" data-bs-toggle="modal" :data-bs-target="'#editInstitution'.concat(institution.id)">Edit</button>
				</div>	
				<div class="col-2">
					<button class="btn btn-danger" :id="institution.id" data-bs-toggle="modal" :data-bs-target="'#deleteInstitution'.concat(institution.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addInstitutionModal" tabindex="-1" aria-labelledby="addInstitutionModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addInstitutionModalHeader">Add Institution</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<textarea v-model="addInstitutionPayload.name" type="text" class="form-control" placeholder="Name of institution"></textarea>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addInstitutionBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="institution in institutions" 
		class="modal fade" 
		:key="'modal_'.concat(institution.id)"
		:id="'editInstitution'.concat(institution.id)" 
		tabindex="-1" 
		aria-labelledby="editInstitutionModal" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editInstitutionModalHeader">Edit Institution</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="institution.institution_name" type="text" class="form-control" placeholder="Data">
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editInstitution(institution.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="institution in institutions" 
		class="modal fade" 
		:key="'modal_'.concat(institution.id)"
		:id="'deleteInstitution'.concat(institution.id)" 
		tabindex="-1" 
		aria-labelledby="deleteInstitutionLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete Institution</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ institution.institution_name }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteInstitution(institution.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>