<script setup>
// Pinia
import { storeToRefs } from 'pinia';
// Vue
import { onMounted, computed } from 'vue';
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useResultStore } from '@/store/result.store';

const resultStore = useResultStore();
const { results, personas } = storeToRefs(resultStore);
const { getResults, getPersonas } = resultStore;

// const testName = results.value[0].test_name == null ? null : results.value[0].test_name; //There are all from the same test descendant

const formattedResults = computed(()=>{
	const arr = [];

	for(var a=0; a<results.value.length; a+= personas.value.length){
		const item = {
			id: results.value[a].id,
			test_name: results.value[a].test_name,
			score: results.value[a].score,
		}


		for(var b=0; b<personas.value.length; b++){
			try {
				item[results.value[a + b].persona_data] = results.value[a + b].persona_value;
			}catch(e){
				e;
			}
			
		}

		arr.push(item);
	}

	return arr;

})

onMounted(()=>{
	getPersonas();
	getResults();
})
</script>

<template>
    <admin-header></admin-header>
    <menu-container></menu-container>
    <div class="container pt-1 mt-5"><!-- 
		<h1 class="text-center h3">Showing results for {{ testName }}</h1> -->
		<div 
			v-if="!results.length && !personas.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No results available</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="result-table-header row bg-primary rounded p-3 m-1 text-light shadow">
				<span 
					class="result-table-header-field text-uppercase col"
					v-for="persona in personas"
					:key="persona.id">
					{{ persona.persona_data }}
				</span>
				<span class="result-table-header-field text-uppercase col">Score</span>
			</div>
			<div class="result-table-body">
				<div 
					class="row m-1 shadow p-2"
					v-for="result in formattedResults"
					:key="result.id"
					>
					<span 
						class="result-table-body-row-col col"
						v-for="persona in personas"
						:key="'persona_x_'.concat(persona.id)">
						{{ result[persona.persona_data] }}
					</span>	
					<span class="col">
						{{ Math.round(result.score) }}
					</span>	
				</div>
				
			</div>
		</div>
	</div>
</template>

