import { defineStore, storeToRefs } from "pinia";
import { ref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'

export const usePersonaStore = defineStore("personas", ()=>{
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);



	const testId = ref(0);

	const personas = ref([]);

	const getPersonas = async ()=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					personas.value = json.data;
				}else {
					appAlert("An error just occured, couldn't get the personas, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to get the personas, please reload the page")
			console.log(e);
		})

		req.open("GET", `${backend.value}/api/persona_data/${testId.value}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const createPersona = async (payload)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const body = JSON.stringify(payload)

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					personas.value.push(json.body[0]);
				}else {
					appAlert("An error just occured, couldn't create the persona, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to create the persona, please reload the page")
			console.log(e);
		})

		req.open("POST", `${backend.value}/api/persona_data/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const editPersona = async (id)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const payload = personas.value.filter((persona)=> persona.id == id)[0];

		const body = JSON.stringify(payload);

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					console.log();
				}else {
					appAlert("An error just occured, couldn't edit the persona, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to edit the persona, please reload the page")
			console.log(e);
		})

		req.open("PUT", `${backend.value}/api/persona_data/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const deletePersona = (id)=>{
		if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						
						const arr = personas.value.filter((persona)=>persona.id != id);
						
						personas.value = arr;
						
					}else {
						appAlert("An error just occured, couldn't delete the persona, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to delete the persona, please reload the page")
				console.log(e);
			})

			req.open("DELETE", `${backend.value}/api/persona_data/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
	}

	return {
		testId,
		personas,
		getPersonas,
		createPersona,
		editPersona,
		deletePersona
	}
})
