import { defineStore, storeToRefs } from "pinia";
import { ref, computed } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'


export const useDashboardStore = defineStore("dashboard", ()=>{
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader } = appStore;

	const adminStore = useAdminStore();
	const { token, institutionId } = storeToRefs(adminStore);


	const numberOfStudentsRegisteredSoFar = ref(0);
	const tests = ref([]);

	const getNumberOfStudentsRegisteredSoFar = ()=>{
		toggleProcessLoader();
		const req = new XMLHttpRequest();
		req.addEventListener("load", ()=>{
			const res = req.response;
			try {
				const json = JSON.parse(res);
				const {body} = json;
				numberOfStudentsRegisteredSoFar.value = body[0].total;
				toggleProcessLoader();
			}catch(e){
				console.log(e);
			}
		});
		req.open("GET", `${backend.value}/api/candidate/total/${institutionId.value}`);
		req.setRequestHeader("Content-Type", "application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`);
		req.send();
	}

	const getTests = ()=>{
		toggleProcessLoader();
		const req = new XMLHttpRequest();
		req.addEventListener("load", ()=>{
			const res = req.response;
			try {
				const json = JSON.parse(res);
				const {body} = json;
				tests.value = body;
				toggleProcessLoader();
			}catch(e){
				console.log(e);
			}
		});
		req.open("GET", `${backend.value}/api/test/institution/${institutionId.value}`);
		req.setRequestHeader("Content-Type", "application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`);
		req.send();
	}

	const activeTests = computed(()=>{
		const arr = tests.value.filter((test)=> test.status == 'active');
		return arr;
	})

	return {
		numberOfStudentsRegisteredSoFar,
		tests,
		activeTests,
		getNumberOfStudentsRegisteredSoFar,
		getTests
	}
})

