
<script setup>
import { 
	onMounted, 
	ref, 
	unref, 
	// computed 
} from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useAnswerStore } from '@/store/answer.store';
import { useQuestionStore } from '@/store/question.store';

// answers
const answerStore = useAnswerStore();
const { questionId, answers } = storeToRefs(answerStore);
const { getAnswers, createAnswer, editAnswer, deleteAnswer, setAsRightAnswer } = answerStore;

// Test
const questionStore = useQuestionStore();
const { questions } = storeToRefs(questionStore);
const currentQuestion = questions.value.filter((question)=> question.id == questionId.value)[0].data;

const searchStr = ref("");

onMounted(()=>{
	getAnswers();
})


// Add test data
const addAnswerPayload = ref({
	questionId: unref(questionId),
	data: ""
})

const addAnswerBtnClick = ()=>{
	createAnswer(addAnswerPayload);
}
</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<h5 class="text-center p-3">
			Showing answers for <span class="text-lowercase">{{ currentQuestion }}</span>
		</h5>
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addAnswerModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getAnswers">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!answers.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No answers available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col text-bold text-light p-2">
					Data
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Is right answer
				</div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="answer in answers"
				:key="answer.id"
				:id="answer.id"
			>
				<div class="col">
					{{ answer.data }}
				</div>
				<div class="col">
					{{ answer.is_right_answer == 1 ? "yes" : "no" }}
				</div>
				<div class="col">
					<button class="btn btn-secondary" @click="setAsRightAnswer(answer.id)">
						{{ answer.is_right_answer == 0 ? "Set as right answer" : "Right answer"}}
					</button>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="answer.id" data-bs-toggle="modal" :data-bs-target="'#editAnswer'.concat(answer.id)">Edit</button>
				</div>	
				<div class="col">
					<button class="btn btn-danger" :id="answer.id" data-bs-toggle="modal" :data-bs-target="'#deleteAnswer'.concat(answer.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addAnswerModal" tabindex="-1" aria-labelledby="addAnswerModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addAnswerModalHeader">Add Answer</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<textarea v-model="addAnswerPayload.data" type="text" class="form-control" placeholder="Data"></textarea>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addAnswerBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="answer in answers" 
		class="modal fade" 
		:key="'modal_'.concat(answer.id)"
		:id="'editAnswer'.concat(answer.id)" 
		tabindex="-1" 
		aria-labelledby="editAnswerLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editAnswerModalheader">Edit answer</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="answer.data" type="text" class="form-control" placeholder="Data">
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editAnswer(answer.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="answer in answers" 
		class="modal fade" 
		:key="'modal_delete'.concat(answer.id)"
		:id="'deleteAnswer'.concat(answer.id)" 
		tabindex="-1" 
		aria-labelledby="deleteAnswerModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete answer</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ answer.data }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteAnswer(answer.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>