
<script setup>
import { storeToRefs } from "pinia";
import { useRouter } from "vue-router";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from "@/store/admin.store";


const appStore = useAppStore();
const { menuState, menuRoutes } = storeToRefs(appStore);
const { toggleMenu } = appStore;

const adminStore = useAdminStore();
const { role } = storeToRefs(adminStore);

const router = useRouter();


const currentRoute = router.currentRoute.value;

const menuWrapperClick = (ev)=>{
	
	const target = ev.target;
	
	if(target.classList.contains("wrapper") > 0){
		toggleMenu();
	}
}

</script>

<template>
	<div 
		v-if="menuState"
		class="wrapper position-fixed top-0 left-0 right-0 bottom-0"
		@click="menuWrapperClick"
	>
		<div class="menu bg-light">
			<ul>
				<li
					v-for="route,index in menuRoutes"
					:key="index" 
					:class="[route.name == currentRoute.name ? 'active' : '', route.meta.isGlobalAdminRoute && role != 'global_admin' ? 'd-none' : '', !route.meta.isGlobalAdminRoute && role == 'global_admin' ? 'd-none' : '']"
					data-aos="slide-in"
					v-ripple="'#0d6efd10'"
				>
					<router-link :to="route.path" class="d-flex align-items-center justify-content-between">
						<span class="icon">
							<i :class="route.icon"></i>
						</span>
						<span class="route-name">
							{{ route.name }}
						</span>
						<span class="indicator">
							.
						</span>
					</router-link>

				</li>
			</ul>
		</div>
	</div>
</template>

<style scoped>
.wrapper {
	backdrop-filter: blur(5px);
	background-color: rgba(0,0,0,.2);
	right: 0;
	left: 0;
	z-index: 10;
}

.menu {
	width: 70%;
	max-width: 20rem;
	box-shadow: 0px 0px 2px black;
	height: 100vh;
}

.active {
	background-color: rgba(0,0,0,.2);
	border-radius: 10px;

}

ul {
	list-style: none;
	padding: 1rem;
}

a {	
	text-decoration: none;
	color: black;
	font-size: 1rem;
	display: block;
}


li {
	padding: 1rem;
}

</style>
 