import { defineStore, storeToRefs } from "pinia";
import { ref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'

export const useUserStore = defineStore("users", ()=>{
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);

	const currentInstitutionId = ref(null);
	const users = ref([]);

	const getUsers = ()=>{
		return new Promise((resolve, reject)=>{
			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// Remove this console.log from production or comment it
						// console.log(json.body)
						users.value = json.body;
						resolve(json);
					}else {
						appAlert("An error just occured, couldn't get the users, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(res);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to get the users, please reload the page")
				console.log(e);
			})

			req.open("GET", `${backend.value}/api/user/${currentInstitutionId.value}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
		})
	}

	const addUser = (payload)=>{
		return new Promise((resolve, reject)=>{
			const body = JSON.stringify(payload.value);
			console.log(body);
			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == "true"){
						// console.log(json)
						users.value.push(json.body[0]);
						resolve(json);
					}else {
						appAlert("An error just occured, couldn't add the user, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(res);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to add the user, please reload the page")
				console.log(e);
			})

			req.open("POST", `${backend.value}/api/user`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		});
	}

	const editUser = (id)=>{
		return new Promise((resolve, reject)=>{
			const payload = users.value.filter((user)=> user.id == id)[0];
			const body = JSON.stringify(payload);
			console.log({payload, body});

			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// console.log(json)
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't edit the user, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(e);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to edit the user, please reload the page")
				console.log(e);
			})

			req.open("PUT", `${backend.value}/api/user`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		});
	}

	const deleteUser = (id)=>{
		return new Promise((resolve, reject)=>{

			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);
					
					if(json.status == true){
						// console.log(json)
						const arr = users.value.filter((user)=> user.id != id);
						users.value = arr;
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't delete the user, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(e);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to delete the user, please reload the page")
				console.log(e);
			})

			req.open("DELETE", `${backend.value}/api/user/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
		});
	}

	return {
		users,
		getUsers,
		addUser,
		editUser,
		deleteUser,
		currentInstitutionId
	}
})
