<script setup>
import ProcessLoader from "@/components/general/ProcessLoader"
import { useAdminStore } from "@/store/admin.store";
import { useCandidateStore } from '@/store/candidate.store';
// import { storeToRefs } from 'pinia';
import { useRouter } from 'vue-router';
import { watchEffect } from 'vue';
const router = useRouter();

watchEffect(()=>{
	const candidateStore = useCandidateStore();
	const adminStore = useAdminStore();
	const candidateToken = candidateStore.token;
	const adminToken = adminStore.token;

	const currentRoute = router.currentRoute.value;
	if(currentRoute.meta.isAdminRoute && currentRoute.requiresAuth && adminToken == ''){
		router.push('/admin');
	}else if(currentRoute.meta.requiresAuth && candidateToken == ''){
		router.push('/');
	}
})
</script>

<template>
	<process-loader></process-loader>
	<!-- <global-modal></global-modal> -->
	<router-view></router-view>
</template>

<style>
#app {
	background: #ffffff;
}	
</style>
