
<script setup>
import { 
	onMounted, 
	ref, 
	// unref, 
	// computed 
} from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useSetupStore } from '@/store/setup.store';
import { useUserStore } from '@/store/user.store';
// users
const userStore = useUserStore();
const { users, currentInstitutionId } = storeToRefs(userStore);
const { getUsers, addUser, editUser, deleteUser } = userStore;

// Setup
const setupStore = useSetupStore();
const { institutions } = storeToRefs(setupStore);
const currentInstitution = institutions.value.filter((institution)=> institution.id == currentInstitutionId.value)[0];

const searchStr = ref("");



const addUserPayload = ref({
	institutionId: currentInstitutionId.value,
	email: '',
	phoneNumber: '',
	password: '',
	role: 'admin'
})

onMounted(()=>{
	getUsers();
})

const addUserBtnClick = ()=>{
	addUser(addUserPayload);
}

</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<h4 class="text-center">Showing users for <span class="text-lowercase">{{ currentInstitution.institution_name }}</span></h4>
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addUserModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getUsers">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!users.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No user available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			{{ JSON.stringify(user) }}
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col-3 text-bold text-light p-2">
					Email
				</div>
				<div class="data-heading col-3 text-bold text-light p-2">
					Phone number
				</div>
				<div class="data-heading col-2"></div>
				<div class="data-heading col-2"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="user in users"
				:key="user.id"
				:id="user.id"
			>
				<div class="col-3">
					{{ user.email }}
				</div>
				<div class="col-3">
					{{ user.phone_number }}
				</div>
				<div class="col-2">
					<button class="btn btn-secondary" :id="user.id" data-bs-toggle="modal" :data-bs-target="'#editUser'.concat(user.id)">Edit</button>
				</div>	
				<div class="col-2">
					<button class="btn btn-danger" :id="user.id" data-bs-toggle="modal" :data-bs-target="'#deleteUser'.concat(user.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addUserModal" tabindex="-1" aria-labelledby="addUserModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addUserModalHeader">Add User</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="addUserPayload.email" type="text" class="form-control" placeholder="Email">
						<br>
						<input v-model="addUserPayload.phoneNumber" type="text" class="form-control" placeholder="Phone number">
						<br>
						<input v-model="addUserPayload.password" type="text" class="form-control" placeholder="Password">
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addUserBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="user in users" 
		class="modal fade" 
		:key="'modal_'.concat(user.id)"
		:id="'editUser'.concat(user.id)" 
		tabindex="-1" 
		aria-labelledby="editUserModal" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editUserModalHeader">Edit User</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="user.email" type="text" class="form-control" placeholder="Data">
						<br>
						<input v-model="user.phone_number" type="text" class="form-control" placeholder="Phone number">
						<br>
						<input v-model="user.password" type="text" class="form-control" placeholder="Password">
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editUser(user.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="user in users" 
		class="modal fade" 
		:key="'modal_'.concat(user.id)"
		:id="'deleteUser'.concat(user.id)" 
		tabindex="-1" 
		aria-labelledby="deleteUserLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete User</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ user.email }}</span>
					<br>
					<span class="text-lowercase">{{ user.phone_number }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteUser(user.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>