
<script setup>
import { 
	onMounted, 
	ref, 
	unref, 
	// computed 
} from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useQuestionStore } from '@/store/question.store';
import { useTestStore } from '@/store/test.store';
import { useAnswerStore } from '@/store/answer.store';

// Vue-router
import { useRouter } from 'vue-router';
const router = useRouter();


// questions
const questionStore = useQuestionStore();
const { testId, questions } = storeToRefs(questionStore);
const { getQuestions, createQuestion, editQuestion, deleteQuestion } = questionStore;

// Test
const testStore = useTestStore();
const { tests } = storeToRefs(testStore);
const currentTestName = tests.value.filter((test)=> test.id == testId.value)[0].test_name

// Answer
const answerStore = useAnswerStore();
const { questionId } = storeToRefs(answerStore);

const searchStr = ref("");

onMounted(()=>{
	getQuestions();
})


// Add test data
const addQuestionPayload = ref({
	testId: unref(testId),
	data: "",
	type: "single_choice"
})

const addQuestionBtnClick = ()=>{
	createQuestion(addQuestionPayload);
}

const answers = (id)=>{
	questionId.value = id;
	router.push('/admin/answers');
}

</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<h5 class="text-center p-3">
			Showing questions for <span class="text-lowercase">{{ currentTestName }}</span>
		</h5>
		<div class="row">
			<div class="col-3">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addQuestionModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="uploadExcel">
					<i class="fa fa-upload"></i> Upload
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getQuestions">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-8">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!questions.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No questions available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col text-bold text-light p-2">
					Data
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Type
				</div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="question in questions"
				:key="question.id"
				:id="question.id"
			>
				<div class="col">
					{{ question.data }}
				</div>
				<div class="col">
					{{ question.type.replace('_',' ') }}
				</div>
				<div class="col">
					<button class="btn btn-secondary" @click="answers(question.id)">Answers</button>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="question.id" data-bs-toggle="modal" :data-bs-target="'#editQuestion'.concat(question.id)">Edit</button>
				</div>	
				<div class="col">
					<button class="btn btn-danger" :id="question.id" data-bs-toggle="modal" :data-bs-target="'#deleteQuestion'.concat(question.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addQuestionModal" tabindex="-1" aria-labelledby="addQuestionModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addQuestionModalHeader">Add Question</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="addQuestionPayload.data" type="text" class="form-control" placeholder="Data">
					</div>
					<div class="p-1">
						<select v-model="addQuestionPayload.type" class="form-select">
							<option disabled>Select test type</option>
							<option value="single_choice">Single choice</option>
							<option value="multiple_choice">Multiple choice</option>
							<option value="true_false">True false</option>
						</select>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addQuestionBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="question in questions" 
		class="modal fade" 
		:key="'modal_'.concat(question.id)"
		:id="'editQuestion'.concat(question.id)" 
		tabindex="-1" 
		aria-labelledby="editQuestionLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editQuestionModalheader">Edit question</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="question.data" type="text" class="form-control" placeholder="Data">
					</div>
					<div class="p-1">
						<select v-model="question.type" class="form-select">
							<option disabled>Select test type</option>
							<option value="single_choice">Single choice</option>
							<option value="multiple_choice">Multiple choice</option>
							<option value="true_false">True false</option>
						</select>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editQuestion(question.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="question in questions" 
		class="modal fade" 
		:key="'modal_delete'.concat(question.id)"
		:id="'deleteQuestion'.concat(question.id)" 
		tabindex="-1" 
		aria-labelledby="deleteQuestionModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete question</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ question.data }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteQuestion(question.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>