import { defineStore, storeToRefs } from "pinia";
import { ref, markRaw } from "vue";
import { useAppStore } from "@/store/app.store";
import router from '@/router/index';


export const useAdminStore = defineStore("admin", ()=>{
	const vRouter = markRaw(router);
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert } = appStore;


	const id = ref("");
	const email = ref("");
	const phoneNumber = ref("");
	const password = ref("");
	const institutionId = ref("");
	const role = ref("");
	const token = ref("");
	

	const login = ()=>{
		const body = JSON.stringify({
					phoneNumber: phoneNumber.value,
					password: password.value
				})


		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);
				if(json.status == true){
					id.value = json.id;
					email.value = json.email;
					token.value = json.token;
					institutionId.value = json.institutionId;
					role.value = json.role;

					if(role.value == 'admin'){
						vRouter.push('/admin/dashboard');
					}else{
						vRouter.push('/admin/setup');
					}
				}else {
					appAlert("An error just occured, please try again in a few seconds");
				}
			}catch(e){
				appAlert("An error just occured, please try again in a few seconds");
			}
		})
		req.open("POST", `${backend.value}/api/auth/signin`);
		req.setRequestHeader("Content-Type","application/json");
		req.send(body);
	}

	return {
		id,
		email,
		phoneNumber,
		password,
		institutionId,
		role,
		token,

		login,
	}
})

