import { defineStore, storeToRefs } from "pinia";
import { ref, markRaw, computed } from "vue";
import { useAppStore } from "@/store/app.store";
import router from '@/router/index';


export const useCandidateStore = defineStore("candidate", ()=>{



	const vRouter = markRaw(router);
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert } = appStore;


	const testCode = ref("");
	const testCredentials = ref({});
	const isValidated = ref(false);
	const candidatePersona = ref([]);
	const details = ref({});
	const questions = ref([]);
	const answers = ref([]);
	const token = ref(null);
	const hasSubmitted = ref(false);

	// Getting user token
	(()=>{
		if(token.value == null){
			toggleProcessLoader();
			const req = new XMLHttpRequest();
			req.addEventListener("load", ()=>{
				toggleProcessLoader();
				const res = req.response;
				try {
					const json = JSON.parse(res);
					if(json.status == true){
						token.value = json.token;
					}
				}catch(e){
					toggleProcessLoader();
					console.log(e);
				}
			});
			req.open("POST", `${backend.value}/api/auth/candidate_token`);
			req.send();
		}
	})();

	const randomizeQuestions = ()=>{
		// Create a new array to avoid modifying the original array
		const randomized = [...questions.value];
		if(testCredentials.value.randomize_questions == true){
			
			// Use Fisher-Yates shuffle algorithm to randomize the order of questions
			for (let i = randomized.length - 1; i > 0; i--) {
				const j = Math.floor(Math.random() * (i + 1));
				[randomized[i], randomized[j]] = [randomized[j], randomized[i]];
			}

			return questions.value = randomized;
		}else {
			return questions.value = randomized;
		}
	}

	function Question(question){
		this.question = question;
		this.question.answered = false;
		this.answers = answers.value.filter((answer)=> answer.question_id == question.id )
	}


	const candidateSheet = ref(null);

	const cursor = ref(0);

	const currentQuestion = computed(()=>{
		return candidateSheet.value[cursor.value];
	})


	const previousQuestion = ()=>{
		if(cursor.value == 0){
			cursor.value = 0;
		}else {
			cursor.value--;
		}
	}

	const nextQuestion = ()=>{
		if(cursor.value == (candidateSheet.value.length-1)){
			cursor.value = candidateSheet.value.length-1;
		}else {
			cursor.value++;
		}
		
	}

	const changeQuestion = (cursorValue)=>{
		cursor.value = cursorValue;
	}

	const markStudent = ()=>{
		return new Promise((resolve)=>{
			const sheet = candidateSheet.value;
			let cummulativeMark = 0;
			const totalMark = testCredentials.value.total_mark;
			const markPerQuestion = totalMark/sheet.length;


			sheet.forEach((item)=>{
				if(item.question.type == 'single_choice'){
					const {answers} = item;
					for(var a=0; a<answers.length; a++){
						if(answers[a].selected && answers[a].is_right_answer){
							cummulativeMark += markPerQuestion;
							break;
						}

					}
				}
			})

			resolve(cummulativeMark);

		})
	}

	const createCandidate = ()=>{
		return new Promise((resolve, reject)=>{
			const body = JSON.stringify({
				testId: testCredentials.value.id,
				candidatePersona: candidatePersona.value,
				details: details.value
			});

			const req = new XMLHttpRequest();
			req.addEventListener("load", ()=>{
				const res = req.response;
				try {
					const json = JSON.parse(res);
					const {insertId} = json.data;
					resolve(insertId);
				}catch(e){
					reject(e);
				}
			});
			req.open("POST", `${backend.value}/api/candidate`);
			req.setRequestHeader("Content-Type", "application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`);
			req.send(body);
		})
	}

	const uploadScore = (testId, candidateId, score)=>{
		return new Promise((resolve, reject)=>{
			const body = JSON.stringify({
				testId,
				candidateId, 
				score
			})

			const req = new XMLHttpRequest();
			req.addEventListener("load", ()=>{
				const res = req.response;
				try {
					const json = JSON.parse(res);
					const {body} = json.body;
					resolve(body);
				}catch(e){
					reject(e);
				}
			});
			req.open("POST", `${backend.value}/api/candidate/score`);
			req.setRequestHeader("Content-Type", "application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`);
			req.send(body);

		})
	}

	const resetApp = ()=>{
		token.value
	}

	const handleSubmit = async ()=>{
		try {
			toggleProcessLoader();
			const insertId = await createCandidate()
			const score = await markStudent();
			const uploadScoreStat = await uploadScore(testCredentials.value.id, insertId, score);
			console.log(uploadScoreStat)
			resetApp();
			vRouter.push("/");
			hasSubmitted.value = true;
			toggleProcessLoader();
		}catch(e){
			console.log(e);
		}
			
		
	}

	const validateTestCode = ()=>{
		let args = 0;
		try {
			args = testCode.value.split("/");
		}catch(e){
			appAlert("Invalid test code");
			return;
		}
	
		const testName = args[0];
		const testId = args[1];
		const institutionId = args[2];



		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);
				if(json.status == true){
					isValidated.value = true;
					testCredentials.value = json.body[0];
					vRouter.push("/form");
				}else {
					appAlert("An error just occured, please try again in a few seconds");
					isValidated.value = false;
					testCredentials.value = null
				}
			}catch(e){
				appAlert("An error just occured, please try again in a few seconds");
				isValidated.value = false;
				testCredentials.value = null
			}
		})
		req.open("GET", `${backend.value}/api/test/validate?testId=${testId}&testName=${testName}&institutionId=${institutionId}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const getPersonaData = ()=>{

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);
				if(json.status == true){
					candidatePersona.value = json.data;

				}else {
					appAlert("An error just occured, please try again in a few seconds");
				}
			}catch(e){
				appAlert("An error just occured, please try again in a few seconds");
			}
		})
		req.open("GET", `${backend.value}/api/persona_data/${testCredentials.value.id}`);
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const getQuestionsAndAnswers = ()=>{
		cursor.value = 0;
		if(hasSubmitted.value == false && candidateSheet.value != null){
			// appAlert("In this condition where I never don sup")
			// Means that candidate sheet is populated
			return;
		}else if(hasSubmitted.value == true && candidateSheet.value != null){
			// appAlert("In the condition where I don sup")
			return new Promise((resolve, reject)=>{

				toggleProcessLoader();
				const req = new XMLHttpRequest();

				// Get questions
				
				req.addEventListener("load", ()=>{
				
					const res = req.response;
					
					try {
						const json = JSON.parse(res);
						if(json.status == true){
							questions.value = json.questions;

							// Get answers 
							const req2 = new XMLHttpRequest();
							req2.addEventListener("load", ()=>{
								
								toggleProcessLoader();
								const res2 = req2.response;
								
								try {
									const json2 = JSON.parse(res2);
									if(json2.status == true){
										const formattedAnswers = json2.answers.map((answer)=>{
											return {
												...answer,
												selected: false
											}
										})

										answers.value = formattedAnswers;

										candidateSheet.value = randomizeQuestions().map((question)=>{
																	return new Question(question);
																});

										console.log(candidateSheet.value)
										resolve(true);
									}else {
										appAlert("An error just occured, please try again in a few seconds");
										reject(false);
									}
								}catch(e){
									appAlert("An error just occured, please try again in a few seconds");
									reject(false);
								}
							})
							req2.open("GET", `${backend.value}/api/answer/test/${testCredentials.value.id}`);
							req2.setRequestHeader("Authorization", `bearer ${token.value}`)
							req2.send();
						}else {
							appAlert("An error just occured, please try again in a few seconds");
							reject(false);
						}
					}catch(e){
						appAlert("An error just occured, please try again in a few seconds");
						reject(false);
					}
				})
				req.open("GET", `${backend.value}/api/question/${testCredentials.value.id}`);
				req.setRequestHeader("Authorization", `bearer ${token.value}`)
				req.send();
			})
		}else {
			// appAlert("Omo I don sup oh")
			return new Promise((resolve, reject)=>{

				toggleProcessLoader();
				const req = new XMLHttpRequest();

				// Get questions
				
				req.addEventListener("load", ()=>{
				
					const res = req.response;
					
					try {
						const json = JSON.parse(res);
						if(json.status == true){
							questions.value = json.questions;

							// Get answers 
							const req2 = new XMLHttpRequest();
							req2.addEventListener("load", ()=>{
								
								toggleProcessLoader();
								const res2 = req2.response;
								
								try {
									const json2 = JSON.parse(res2);
									if(json2.status == true){
										const formattedAnswers = json2.answers.map((answer)=>{
											return {
												...answer,
												selected: false
											}
										})

										answers.value = formattedAnswers;

										candidateSheet.value = randomizeQuestions().map((question)=>{
																	return new Question(question);
																});

										console.log(candidateSheet.value)
										resolve(true);
									}else {
										appAlert("An error just occured, please try again in a few seconds");
										reject(false);
									}
								}catch(e){
									appAlert("An error just occured, please try again in a few seconds");
									reject(false);
								}
							})
							req2.open("GET", `${backend.value}/api/answer/test/${testCredentials.value.id}`);
							req2.setRequestHeader("Authorization", `bearer ${token.value}`)
							req2.send();
						}else {
							appAlert("An error just occured, please try again in a few seconds");
							reject(false);
						}
					}catch(e){
						appAlert("An error just occured, please try again in a few seconds");
						reject(false);
					}
				})
				req.open("GET", `${backend.value}/api/question/${testCredentials.value.id}`);
				req.setRequestHeader("Authorization", `bearer ${token.value}`)
				req.send();
			})
		}
		
		

	}

	const updateSingleChoiceAnswerStatus = (concAnswerId)=>{
		const answerId = concAnswerId.split("_")[1];
		const questionId = answers.value.filter((answer)=> answer.id == answerId)[0].question_id;
		// console.log({questionId})
		for(var i=0; i<candidateSheet.value.length; i++){
		
			if(candidateSheet.value[i].question.id == questionId){
				for(var j=0; j<candidateSheet.value[i].answers.length; j++){
					if(candidateSheet.value[i].answers[j].id == answerId){
						candidateSheet.value[i].answers[j].selected = true;
					}else {
						candidateSheet.value[i].answers[j].selected = false;	
					}
				}
				candidateSheet.value[i].question.answered = true;
				break;
			}

		}
		
	}

	

	return {
		testCode,
		isValidated,
		validateTestCode,
		testCredentials,
		getPersonaData,
		candidatePersona,
		details,
		getQuestionsAndAnswers,
		currentQuestion,
		candidateSheet,
		questions,
		answers,
		previousQuestion,
		nextQuestion,
		cursor,
		updateSingleChoiceAnswerStatus,
		changeQuestion,
		handleSubmit,
		token
	}
})

