import { ref, unref } from 'vue';
import { defineStore, storeToRefs } from "pinia";
import { useAppStore } from "./app.store";
import { useAdminStore } from "./admin.store";

export const useTestStore = defineStore("tests", ()=>{

	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token, institutionId } = storeToRefs(adminStore);

	const tests = ref([]);

	const createTest = (payload)=>{
		// if(token.value == ""){
		// 	appAlert("An error just occured try please again");
		// 	return;
		// }

		const body = JSON.stringify(unref(payload));

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			
			try {
				const json = JSON.parse(res);
				
				if(json.status == true){
					const { body } = json;
					tests.value.push(body[0]);
				}else {
					appAlert("An error just occured, please try again in a few seconds");
				}
			}catch(e){
				appAlert("An error just occured, please try again in a few seconds");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to create the new test, please try again")
			console.log(e);
		})

		req.open("POST", `${backend.value}/api/test`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const editTest = (id)=>{
		// if(token.value == ""){
		// 	appAlert("An error just occured try please again");
		// 	return;
		// }

		const testToEdit = unref(tests).filter((test)=> test.id == id);

		const body = JSON.stringify(testToEdit[0]);

		// console.log({
		// 	testToEdit,
		// 	body
		// })

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);
				
				if(json.status == true){
					console.log();
				}else {
					appAlert("An error just occured, please try again in a few seconds");
				}
			}catch(e){
				console.log(e);
				appAlert("An error just occured, please try again in a few seconds");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to edit the test, please try again")
			console.log(e);
		})

		req.open("PUT", `${backend.value}/api/test`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const deleteTest = (id)=>{

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);
				
				if(json.status == true){
					const newTestArr = tests.value.filter((test)=>test.id != id);
					// console.log(newTestArr)
					tests.value = newTestArr;
				}else {
					appAlert("An error just occured, please try again in a few seconds");
				}
			}catch(e){
				console.log(e);
				appAlert("An error just occured, please try again in a few seconds");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to delete the test, please try again")
			console.log(e);
		})

		req.open("DELETE", `${backend.value}/api/test/${id}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}


	const getTests = ()=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					tests.value = json.body;
				}else {
					appAlert("An error just occured, couldn't get the tests, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to get the tests, please reload the page")
			console.log(e);
		})

		req.open("GET", `${backend.value}/api/test/institution/${institutionId.value}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const handleStatusChange = (id)=>{
		const test = tests.value.filter((test)=> test.id == id)[0];
		const body = JSON.stringify({id});
		if(test.status == 'active'){
			// Stop the test
			

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// Update the tests store
						for(var i=0; i<tests.value.length; i++){
							if(tests.value[i] == test){
								tests.value[i].status = 'inactive';
								break;
							}
						}
					}else {
						appAlert("An error just occured, couldn't start the tests, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to start the tests, please reload the page")
				console.log(e);
			})

			req.open("POST", `${backend.value}/api/test/stop`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		}else {
			// Start the test
			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// Update the tests store
						for(var i=0; i<tests.value.length; i++){
							if(tests.value[i] == test){
								tests.value[i].status = 'active';
								break;
							}
						}
					}else {
						appAlert("An error just occured, couldn't stop the tests, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to stop the tests, please reload the page")
				console.log(e);
			})

			req.open("POST", `${backend.value}/api/test/start`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		}
	}



	return {
		tests,
		createTest,
		editTest,
		getTests,
		deleteTest,
		handleStatusChange
	}
})