import { defineStore, storeToRefs } from "pinia";
import { ref, unref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'

export const useAnswerStore = defineStore("answers", ()=>{
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);



	const questionId = ref(0);

	const answers = ref([]);

	const getAnswers = async ()=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					// console.log(json)
					answers.value = json.answers;
				}else {
					appAlert("An error just occured, couldn't get the answers, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to get the answers, please reload the page")
			console.log(e);
		})

		req.open("GET", `${backend.value}/api/answer/${questionId.value}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const createAnswer = async (payload)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const body = JSON.stringify({
			questionId: questionId.value,
			data: unref(payload).data,
			type: unref(payload).type
		})

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){

					// console.log(json);
					answers.value.push(json.body[0]);
				}else {
					appAlert("An error just occured, couldn't create the answer, refreshing");

				}
			}catch(e){
				console.log(e);
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to create the answer, please reload the page")
			console.log(e);
		})

		req.open("POST", `${backend.value}/api/answer/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const editAnswer = async (id)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const answer = answers.value.filter((answer)=> answer.id == id)[0];

		const body = JSON.stringify(answer);

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					// console.log(json)
					// alert(res);
					console.log();
					
				}else {
					appAlert("An error just occured, couldn't edit the answer, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to edit the answer, please reload the page")
			console.log(e);
		})

		req.open("PUT", `${backend.value}/api/answer/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const deleteAnswer = (id)=>{
		if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// console.log(json)
						// alert(res);
						const arr = answers.value.filter((test)=>test.id != id);
						answers.value = arr;
						
					}else {
						appAlert("An error just occured, couldn't edit the answer, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to edit the answer, please reload the page")
				console.log(e);
			})

			req.open("DELETE", `${backend.value}/api/answer/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
	}

	const setAsRightAnswer = (id)=>{
		if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				toggleProcessLoader();
				const res = req.response;
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						const answer = answers.value.filter((test)=>test.id == id)[0];
						for(var i=0; i<answers.value.length; i++){
	
							if(answers.value[i] == answer){
		
								answers.value[i].is_right_answer = 1;
							}else {
								answers.value[i].is_right_answer = 0;
							}
						}

						
					}else {
						appAlert("An error just occured, couldn't set as right answer, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to set as right answer, please reload the page")
				console.log(e);
			})

			req.open("POST", `${backend.value}/api/answer/set_as_right_answer/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
	}

	return {
		questionId,
		answers,
		getAnswers,
		createAnswer,
		editAnswer,
		deleteAnswer,
		setAsRightAnswer
	}
})
