import components from './components';

export default [
    // Candidate
    {
        name: 'Cebety',
        path: '/',
        component: components.CandidateIndex,
        meta: {
            isAdminRoute: false,
            requiresAuth: false
        }
    },
    {
        name: 'Candidate Test',
        path: '/test',
        component: components.CandidateTestView,
         meta: {
            isAdminRoute: false,
            requiresAuth: true
        }
    },
    {
        name: 'Candidate form',
        path: '/form',
        component: components.CandidateFormView,
         meta: {
            isAdminRoute: false,
            requiresAuth: true
        }
    },
    {
        name: 'End Test',
        path: '/end_test',
        component: components.EndTestView,
        meta: {
            isAdminRoute: false,
            requiresAuth: true
        }
    },
    // Admin
    {
        name: 'Login',
        path: '/admin',
        component: components.AdminIndex,
         meta: {
            isAdminRoute: true,
            requiresAuth: false,
            showInMenu: false,
        }
    },
    {
        name: 'Dashboard',
        path: '/admin/dashboard',
        component: components.DashboardView,
         meta: {
            isAdminRoute: true,
            requiresAuth: true,
            showInMenu: true,
            showInMenuName: 'Dashboard',
            showInMenuIcon: 'fa fa-dashboard'
        }
    },
    {
        name: 'Result',
        path: '/admin/result',
        component: components.ResultView,
        meta: {
            isAdminRoute: true,
            requiresAuth: true,
            showInMenu: true,
            showInMenuName: 'Dashboard',
            showInMenuIcon: 'fa fa-excel'
        }
    },
    {
        name: 'Setup',
        path: '/admin/setup',
        component: components.SetupView,
         meta: {
            isAdminRoute: true,
            isGlobalAdminRoute: true,
            requiresAuth: true,
            showInMenu: true,
            showInMenuName: 'Setup',
            showInMenuIcon: 'fa fa-wrench'
        }
    },
    {
        name: 'Users',
        path: '/admin/users',
        component: components.UserView,
         meta: {
            isAdminRoute: true,
            isGlobalAdminRoute: true,
            requiresAuth: true,
            showInMenu: true,
            showInMenuName: 'Setup',
            showInMenuIcon: 'fa fa-users'
        }
    },
    {
        name: 'Test',
        path: '/admin/test',
        component: components.TestView,
         meta: {
            isAdminRoute: true,
            requiresAuth: true,
            showInMenu: true,
            showInMenuName: 'Test',
            showInMenuIcon: 'fa fa-graduation-cap'
        }
    },
    {
        name: 'Questions',
        path: '/admin/questions',
        component: components.QuestionView,
         meta: {
            isAdminRoute: true,
            requiresAuth: true
        }
    },
    {
        name: 'Answers',
        path: '/admin/answers',
        component: components.AnswerView,
         meta: {
            isAdminRoute: true,
            requiresAuth: true
        }
    },
    {
        name: 'Persona data',
        path: '/admin/persona',
        component: components.PersonaView,
         meta: {
            isAdminRoute: true,
            requiresAuth: true
        }
    }
]