<script setup>
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { onMounted } from 'vue';
import { storeToRefs } from 'pinia';
import { useRouter } from 'vue-router';
import { useAdminStore } from '@/store/admin.store';
import { useDashboardStore } from '@/store/dashboard.store';

const adminStore = useAdminStore();
const { role } = storeToRefs(adminStore);

const dashboardStore = useDashboardStore();
const { tests, numberOfStudentsRegisteredSoFar, activeTests } = storeToRefs(dashboardStore);
const { getNumberOfStudentsRegisteredSoFar, getTests } = dashboardStore;


onMounted(()=>{
	if(role == 'global_admin'){
		const router = useRouter();
		router.replace('/admin/setup');
	}else {
		getNumberOfStudentsRegisteredSoFar();
		getTests();
	}
});
</script>

<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<div class="row">
			<div class="col-lg-6 col-md-6 col-sm-12 p-2">
				<div class="card">
					<div class="card-header">
						<h5 class="card-title"><i class="fas fa-users"></i> Number of Students Registered So Far</h5>
					</div>
					<div class="card-body">
						<p class="card-text">There are currently {{ numberOfStudentsRegisteredSoFar }} students that have taken tests for this institution</p>
					</div>
					<div class="card-footer">
						<small class="text-muted"><i class="far fa-clock"></i> Retrieved {{ new Date() }}
						</small>
					</div>
				</div>

			</div>
			<div class="col-lg-6 col-md-6 col-sm-12 p-2">
				<div class="card">
					<div class="card-header">
						<h5 class="card-title"><i class="fas fa-file-alt"></i> Number of Tests</h5>
					</div>
					<div class="card-body">
						<p class="card-text">There are currently {{ tests.length }} tests</p>
					</div>
					<div class="card-footer">
						<small class="text-muted"><i class="far fa-calendar-alt"></i> <router-link to='/admin/test/'>See more</router-link></small>
					</div>
				</div>

			</div>
			<div class="col p-2">
				<div class="card">
					<div class="card-header">
					<h5 class="card-title"><i class="fas fa-tasks"></i> Active Tests</h5>
				</div>
				<div class="card-body">
					<ul class="list-group list-group-flush">
						<li class="list-group-item" v-for="test in activeTests" :key="test.id">{{ test.test_name }}</li>
					</ul>
				</div>
				<div class="card-footer">
					<small class="text-muted"><i class="far fa-calendar-alt"></i> <router-link to='/admin/test/'>See more</router-link></small>
				</div>
			</div>

			</div>		
		</div>
	</div>
</template>
