<template>
    <div v-if="isMenuRoute" class="p-1 text-light" v-ripple="'rgba(255,255,255,.2)'" @click="toggleMenu">
        <i class="fa fa-bars fa-2x"></i>
    </div>

    <div v-else class="p-1 text-light" @click="this.$router.back()">
        <i class="fa fa-arrow-left fa-2x"></i>
    </div>

</template>

<script setup>
import { storeToRefs } from "pinia";
import { useAppStore } from "@/store/app.store";

import { useRouter } from "vue-router";
import { computed,unref } from "vue";


const appStore = useAppStore();
const { menuRoutes } = storeToRefs(appStore);
const {toggleMenu } = appStore;

const router = useRouter();
const currentRoute = router.currentRoute.value;

const isMenuRoute = computed(()=>{
    let status = false;

    const routes = unref(menuRoutes);
    for(var i=0; i<routes.length; i++){
        if(routes[i].name == currentRoute.name){
            status = true;
            break;
        } 
    }

    return status;
})
</script>

<style scoped>
div {
    display: inline-block;
    width: 3rem;
    height: 3rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 100ms ease-in;
    cursor: pointer;
}

</style>