import { defineStore, storeToRefs } from "pinia";
import { ref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'

export const useSetupStore = defineStore("setup", ()=>{
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);

	const institutions = ref([]);

	const getInstitutions = ()=>{
		return new Promise((resolve, reject)=>{
			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// console.log(json)
						institutions.value = json.body;
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't get the institutions, refreshing");
						reject(false);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(false);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to get the institutions, please reload the page")
				console.log(e);
			})

			req.open("GET", `${backend.value}/api/institution/all`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
		})
	}

	const addInstitution = (payload)=>{
		return new Promise((resolve, reject)=>{
			const body = JSON.stringify(payload.value);
			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// console.log(json)
						institutions.value.push(json.body[0]);
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't add the institution, refreshing");
						reject(false);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(false);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to add the institution, please reload the page")
				console.log(e);
			})

			req.open("POST", `${backend.value}/api/institution`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		});
	}

	const editInstitution = (id)=>{
		return new Promise((resolve, reject)=>{
			const payload = institutions.value.filter((institution)=> institution.id == id)[0];
			const body = JSON.stringify(payload);
			console.log({payload, body});

			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == 'true'){
						// console.log(json)
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't edit the institution, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(e);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to edit the institution, please reload the page")
				console.log(e);
			})

			req.open("PUT", `${backend.value}/api/institution`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send(body);
		});
	}

	const deleteInstitution = (id)=>{
		return new Promise((resolve, reject)=>{

			if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);
					
					if(json.status == true){
						// console.log(json)
						const arr = institutions.value.filter((institution)=> institution.id != id);
						institutions.value = arr;
						resolve(true);
					}else {
						appAlert("An error just occured, couldn't delete the institution, refreshing");
						reject(res);
					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
					reject(e);
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to delete the institution, please reload the page")
				console.log(e);
			})

			req.open("DELETE", `${backend.value}/api/institution/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
		});
	}

	return {
		institutions,
		getInstitutions,
		addInstitution,
		editInstitution,
		deleteInstitution
	}
})
