import { defineStore, storeToRefs } from "pinia";
import { ref, unref } from "vue";
import { useAppStore } from "@/store/app.store";
import { useAdminStore } from '@/store/admin.store'
// import { useFileDialog } from '@vueuse/core';

export const useQuestionStore = defineStore("questions", ()=>{
	
	const appStore = useAppStore();
	const { backend } = storeToRefs(appStore);
	const { toggleProcessLoader, appAlert} = appStore;

	const adminStore = useAdminStore();
	const { token } = storeToRefs(adminStore);



	const testId = ref(0);

	const questions = ref([]);

	const getQuestions = async ()=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					// console.log(json)
					questions.value = json.questions;
				}else {
					appAlert("An error just occured, couldn't get the questions, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to get the questions, please reload the page")
			console.log(e);
		})

		req.open("GET", `${backend.value}/api/question/${testId.value}`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send();
	}

	const createQuestion = async (payload)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const body = JSON.stringify({
			testId: testId.value,
			data: unref(payload).data,
			type: unref(payload).type
		})

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					// console.log(json)
					// alert(res);
					questions.value.push(json.body[0]);
				}else {
					appAlert("An error just occured, couldn't create the question, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to create the question, please reload the page")
			console.log(e);
		})

		req.open("POST", `${backend.value}/api/question/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const editQuestion = async (id)=>{
		if(token.value == ""){
			appAlert("An error just occured please try again");
			return;
		}

		const question = questions.value.filter((question)=> question.id == id)[0];

		const body = JSON.stringify(question);

		toggleProcessLoader();
		const req = new XMLHttpRequest();
		
		req.addEventListener("load", ()=>{
			
			toggleProcessLoader();
			const res = req.response;
			// appAlert(res);
			try {
				const json = JSON.parse(res);

				if(json.status == true){
					// console.log(json)
					// alert(res);
					console.log();
					
				}else {
					appAlert("An error just occured, couldn't edit the question, refreshing");

				}
			}catch(e){
				appAlert("An error just occured, it seems to be internal, please contact the admin");
			}
		})

		req.addEventListener("error", (e)=>{
			appAlert("An error just occured while trying to edit the question, please reload the page")
			console.log(e);
		})

		req.open("PUT", `${backend.value}/api/question/`);
		req.setRequestHeader("Content-Type","application/json");
		req.setRequestHeader("Authorization", `bearer ${token.value}`)
		req.send(body);
	}

	const deleteQuestion = (id)=>{
		if(token.value == ""){
				appAlert("An error just occured please try again");
				return;
			}

			toggleProcessLoader();
			const req = new XMLHttpRequest();
			
			req.addEventListener("load", ()=>{
				
				toggleProcessLoader();
				const res = req.response;
				// appAlert(res);
				try {
					const json = JSON.parse(res);

					if(json.status == true){
						// console.log(json)
						// alert(res);
						const arr = questions.value.filter((test)=>test.id != id);
						questions.value = arr;
						
					}else {
						appAlert("An error just occured, couldn't edit the question, refreshing");

					}
				}catch(e){
					appAlert("An error just occured, it seems to be internal, please contact the admin");
				}
			})

			req.addEventListener("error", (e)=>{
				appAlert("An error just occured while trying to edit the question, please reload the page")
				console.log(e);
			})

			req.open("DELETE", `${backend.value}/api/question/${id}`);
			req.setRequestHeader("Content-Type","application/json");
			req.setRequestHeader("Authorization", `bearer ${token.value}`)
			req.send();
	}

	// const uploadExcel = ()=>{
	// 	const { files, open, reset, onChange } = useFileDialog();

	// 	onChange((file)=>{
	// 		const bool = confirm("Upload excel file");
	// 		if(bool){
	// 			const fileReader = new FileReader();
	// 			fileReader.addEventListener("load", ()=>{

	// 			})
	// 		}
	// 	})

	// 	const file = acceptedFiles.value[0];

	// 	const xhr = new XMLHttpRequest();
	// 	xhr.open('POST', `/api/test/excel/${testId.value}`);
	// 	xhr.setRequestHeader('Content-Type', 'application/vnd.ms-excel');
	// 	xhr.addEventListener("load", ()=>{
	// 		toggleProcessLoader();
	// 		const res = xhr.response;
	// 		try {
	// 			const json = JSON.parse(res);

	// 			if(json.status == true){
	// 				// Update the tests store
	// 				getQuestions();
	// 			}else {
	// 				appAlert("An error just occured, couldn't stop the tests, refreshing");

	// 			}
	// 		}catch(e){
	// 			appAlert("An error just occured, it seems to be internal, please contact the admin");
	// 		}
	// 	})
	// 	xhr.send(file);
	// }

	return {
		testId,
		questions,
		getQuestions,
		createQuestion,
		editQuestion,
		deleteQuestion
		// uploadExcel
	}
})
