// Vue
import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import 'vue3-easy-data-table/dist/style.css';

// Router
import router from './router'

// Vue-ripple-directive
import Ripple from 'vue3-whr-ripple-directive'

// Pinia, pinia-plugin-persistedstate-2 and localforage for persistent statemanagement
// import localforage from 'localforage'
// let appStorage = window.indexedDB ? localforage : localStorage;
import { createPersistedStatePlugin } from 'pinia-plugin-persistedstate-2'
import { createPinia } from 'pinia'

const pinia = createPinia();
pinia.use(createPersistedStatePlugin())

// Animate on scroll library
import AOS from 'aos'
import 'aos/dist/aos.css';

// Boostrap
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

// Fontawesome
import '@/assets/css/fontawesome-all.min.css';

// CSS dependencies
import '@/assets/css/utils.css';


const app = createApp(App);
app.use(pinia);
app.use(router);

// Directives
app.directive('ripple', Ripple);

// Mount the vue app to the #app element
app.mount('#app');

app.mixin({
    mounted() {
        AOS.init()
    }
})