import { defineStore } from "pinia";
import { ref } from "vue";

export const useAppStore = defineStore("app", ()=>{
	const backend = ref("http://localhost:9000");
	const processLoading = ref(false);

	const menuState = ref(false);

	const menuRoutes = ref([
			{
				name: 'Dashboard',
				icon: 'fa fa-tachometer-alt',
				path: '/admin/dashboard',
				meta: {
					isGlobalAdminRoute: false
				}
			},
			{
				name: 'Test',
				icon: 'fa fa-graduation-cap',
				path: '/admin/test',
				meta: {
					isGlobalAdminRoute: false
				}
			},
			{
				name: 'Setup',
				icon: 'fas fa-cog',
				path: '/admin/setup',
				meta: {
					isGlobalAdminRoute: true
				}
			}
		])


	const toggleMenu = ()=>{
		menuState.value = !menuState.value;
	}

	const appAlert = (options)=>{
		if(options.message){
			alert(options.message);
		}else {
			alert(options);
		}
		
	}

	const toggleProcessLoader = ()=>{
		processLoading.value = !processLoading.value;
	}

	return {
		backend,
		processLoading,
		toggleProcessLoader,
		menuState,
		toggleMenu,
		appAlert,
		menuRoutes
	}
}
,{
	persistedState: {
		persist: false
	}
})
