
<script setup>
import { onMounted, ref, unref } from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

import { useTestStore } from '@/store/test.store';
// Admin store
import { useAdminStore } from '@/store/admin.store';
// Question store
import { useQuestionStore } from '@/store/question.store';
// Persona store
import { usePersonaStore } from '@/store/persona.store';
// Result store
import { useResultStore } from '@/store/result.store';
// Vue-router
import { useRouter } from 'vue-router';
const router = useRouter();

// tests
const testStore = useTestStore();
const { tests } = storeToRefs(testStore);
const { getTests, createTest, editTest, deleteTest, handleStatusChange } = testStore;

// Questions
const questionStore = useQuestionStore();
const { testId } = storeToRefs(questionStore);

// Result
const resultStore = useResultStore();
const { currentTestId } = storeToRefs(resultStore);

const searchStr = ref("");

// const filteredTests = computed(()=>{
// 	const filtered = tests
// 	return filtered;
// })

// admin
const adminStore = useAdminStore();
const { institutionId } = storeToRefs(adminStore);

onMounted(()=>{
	getTests();
})


// Add test data
const addTestPayload = ref({
	name: "",
	institutionId: unref(institutionId),
	duration:"30",
	type:"manual",
	timeProtocol:"client",
	startTime:"",
	endTime:"",
	usePreRegisteredCandidates:"true",
	randomizeQuestions:"true",
	totalMark: 100
})

const addTestBtnClick = ()=>{
	createTest(addTestPayload);
}

const questions = (id)=>{
	testId.value = id;
	router.push('/admin/questions');
}

const personaStore = usePersonaStore();

const persona = (id)=>{
	personaStore.testId = id;
	router.push('/admin/persona');
}

const result = (id)=>{
	currentTestId.value = id;
	router.push('/admin/result');
}

</script>


<template>
    <admin-header></admin-header>
    <menu-container></menu-container>
    <div class="container pt-5 mt-5">
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addTestModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getTests">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!tests.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No tests available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col text-bold text-light p-2">
					Name
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Duration
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Status
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Test code
				</div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="test in tests"
				:key="test.id"
				:id="test.id"
			>
				<div class="col">
					{{ test.test_name }}
				</div>
				<div class="col">
					{{ test.duration }}
				</div>
				<div class="col">
					{{ test.status }}
				</div>
				<div class="col">
					{{ test.test_name }}/{{ test.id }}/{{ test.institution_id }}
				</div>
				<div class="col">
					<button :class="['btn', (test.status != 'active') ? 'btn-info' : 'btn-danger']" data-bs-toggle="modal" :data-bs-target="'#startTestModal'.concat(test.id)">
						{{ test.status != 'active' ? 'start' : 'stop' }}
					</button>
				</div>
				<div class="col">
					<button class="btn btn-secondary" @click="questions(test.id)">Questions</button>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="test.id" @click="persona(test.id)">Persona</button>
				</div>	
				<div class="col">
					<button class="btn btn-secondary" :id="test.id" @click="result(test.id)">Results</button>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="test.id" data-bs-toggle="modal" :data-bs-target="'#editTestModal'.concat(test.id)">Edit</button>
				</div>	
				<div class="col">
					<button class="btn btn-danger" :id="test.id" data-bs-toggle="modal" :data-bs-target="'#deleteTestModal'.concat(test.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addTestModal" tabindex="-1" aria-labelledby="addTestModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addTestModalLabel">Add Test</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="addTestPayload.name" type="text" class="form-control" placeholder="Name">
					</div>
					<div class="p-1">
						<input v-model="addTestPayload.duration" type="number" class="form-control" min="1" placeholder="Duration">
					</div>
					<div class="p-1">
						<select v-model="addTestPayload.type" class="form-select">
							<option disabled>Select test type</option>
							<option value="manual">Manual</option>
							<option value="scheduled">Scheduled</option>
						</select>
					</div>
					<div class="p-1">
						<select v-model="addTestPayload.timeProtocol" class="form-select">
							<option disabled>Select time protocol</option>
							<option value="server">Server</option>
							<option value="client" >Client</option>
						</select>
					</div>
					<div class="p-1">
						<input v-model="addTestPayload.totalMark" type="number" class="form-control" placeholder="Total mark">
					</div>
					<div class="p-1">
						<label>Randomize questions <input type="checkbox" v-model="addTestPayload.randomizeQuestions"></label>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addTestBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="test in tests" 
		class="modal fade" 
		:key="'modal_'.concat(test.id)"
		:id="'editTestModal'.concat(test.id)" 
		tabindex="-1" 
		aria-labelledby="editTestModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editTestModalHeader">Edit Test</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="test.test_name" type="text" class="form-control" placeholder="Name">
					</div>
					<div class="p-1">
						<input v-model="test.duration" type="number" class="form-control" min="1" placeholder="Duration">
					</div>
					<div class="p-1">
						<select v-model="test.type" class="form-select">
							<option disabled>Select test type</option>
							<option value="manual">Manual</option>
							<option value="scheduled">Scheduled</option>
						</select>
					</div>
					<div class="p-1">
						<select v-model="test.time_protocol" class="form-select">
							<option disabled>Select time protocol</option>
							<option value="server">Server</option>
							<option value="client" >Client</option>
						</select>
					</div>
					<div class="p-1">
						<input v-model="test.total_mark" type="number" class="form-control" placeholder="Total mark">
					</div>
					<div class="p-1">
						<label>Randomize questions <input type="checkbox" v-model="test.randomize_questions"></label>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editTest(test.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Delete Modals -->
	<div
		v-for="test in tests" 
		class="modal fade" 
		:key="'modal_delete'.concat(test.id)"
		:id="'deleteTestModal'.concat(test.id)" 
		tabindex="-1" 
		aria-labelledby="deleteTestModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="deleteTestModalHeader">Delete Test</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					Delete this <span class="text-lowercase">{{ test.test_name }}</span> test?
					
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deleteTest(test.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

		<!-- Start Modals -->
	<div
		v-for="test in tests" 
		class="modal fade" 
		:key="'modal_start'.concat(test.id)"
		:id="'startTestModal'.concat(test.id)" 
		tabindex="-1" 
		aria-labelledby="startTestModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="startTestModalHeader">{{ test.status == 'active' ? 'Stop' : 'Start' }} Test</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					{{ test.status == 'active' ? 'Stop' : 'Start' }} this <span class="text-lowercase">{{ test.test_name }}</span> test?
					
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="handleStatusChange(test.id)" data-bs-dismiss="modal" :class="['btn', (test.status == 'active') ? 'btn-danger' : 'btn-info']">Yes</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>