
<script setup>
import { onMounted, ref, unref } from 'vue';
import { storeToRefs } from 'pinia'
// Components
import MenuContainer from '@/components/menu/MenuContainer/MenuContainer';
import AdminHeader from '@/components/header/AdminHeader';

// Persona store
import { usePersonaStore } from '@/store/persona.store';

// Test store
import { useTestStore } from '@/store/test.store';

const personaStore = usePersonaStore();
const { testId, personas } = storeToRefs(personaStore);
const { getPersonas, createPersona, editPersona, deletePersona } = personaStore;



const testStore = useTestStore();
const { tests } = storeToRefs(testStore);
const currentTestName = tests.value.filter((test)=> test.id == testId.value)[0].test_name


const addPersonaPayload = ref({
	testId: unref(testId),
	personaData: "",
	personaFieldType: "text",
	isRequired: "false",
	isUnique: "false"
})

const addPersonaBtnClick = ()=>{
	createPersona(unref(addPersonaPayload));
}

onMounted(()=>{
	getPersonas();
})

const searchStr = ref("");
</script>


<template>
	<admin-header></admin-header>
	<menu-container></menu-container>
	<div class="container pt-5 mt-5">
		<h5 class="text-center p-3">
			Showing persona data for <span class="text-lowercase">{{ currentTestName }}</span>
		</h5>
		<div class="row">
			<div class="col-2">
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#addPersonaModal">
					<i class="fa fa-plus"></i> add
				</button>
				&nbsp;
				<button v-ripple="'#0d6efd10'" class="btn btn-primary" type="button" @click="getPersonas">
					<i class="fa fa-spinner"></i>
				</button>
			</div>		
			<div class="col-10">
				<div class="input-group">
					<input type="text" v-model="searchStr" class="form-control">
					<button class="btn btn-primary">
						<i class="fa fa-search"></i> search
					</button>
				</div>
				
			</div>
		</div>
		<div 
			v-if="!personas.length"
			class="container-fluid p-2 d-flex align-items-center justify-content-center" style="flex-direction: column">
			<img src="@/assets/images/yetti.png" id="no_test">
			<br>
			<h5>No personas available please add a new one</h5>
		</div>

		<div v-else class="container mt-3">
			<div class="data-header row bg-primary rounded">
				<div class="data-heading col text-bold text-light p-2">
					Data
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Field type
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Is unique
				</div>
				<div class="data-heading col text-bold text-light p-2">
					Is required
				</div>
				<div class="data-heading col"></div>
				<div class="data-heading col"></div>
			</div> 
			<div 
				class="data-item row p-2 bg-light rounded mt-1"
				v-for="persona in personas"
				:key="persona.id"
				:id="persona.id"
			>
				<div class="col">
					{{ persona.persona_data }}
				</div>
				<div class="col">
					{{ persona.persona_field_type }}
				</div>
				<div class="col">
					<span :class="[persona.is_unique == 'false' ? 'text-danger' : 'text-dark' ]">{{ persona.is_unique == false  }}</span>
				</div>
				<div class="col">
					<span :class="[persona.is_required == 'false' ? 'text-danger' : 'text-dark' ]">{{ persona.is_required  }}</span>
				</div>
				<div class="col">
					<button class="btn btn-secondary" :id="persona.id" data-bs-toggle="modal" :data-bs-target="'#editPersona'.concat(persona.id)">Edit</button>
				</div>	
				<div class="col">
					<button class="btn btn-danger" :id="persona.id" data-bs-toggle="modal" :data-bs-target="'#deletePersona'.concat(persona.id)">Delete</button>
				</div>	
			</div>
		</div>
	</div>

	<!-- Add Modal -->
	<div class="modal fade" id="addPersonaModal" tabindex="-1" aria-labelledby="addPersonaModalLabel" aria-hidden="true">
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="addPersonaModalHeader">Add Persona</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
				</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="addPersonaPayload.personaData" type="text" class="form-control" placeholder="Data">
					</div>
					<div class="p-1">
						<select v-model="addPersonaPayload.personaFieldType" type="text" class="form-select">
							<option disabled>--Select field type--</option>
							<option value="text">Text</option>
							<option value="number">Number</option>
							<option value="email">Email</option>
						</select>
					</div>
					<div class="p-1">
						<label class="form-control d-flex justify-content-between">
							The data is unique <input type="checkbox" v-model="addPersonaPayload.isUnique">
						</label>
					</div>
					<div class="p-1">
						<label class="form-control d-flex justify-content-between">
							The data is required <input type="checkbox" v-model="addPersonaPayload.isRequired">
						</label>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="addPersonaBtnClick" data-bs-dismiss="modal" class="btn btn-primary">Add</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>

	<!-- Edit Modals -->
	<div
		v-for="persona in personas" 
		class="modal fade" 
		:key="'modal_'.concat(persona.id)"
		:id="'editPersona'.concat(persona.id)" 
		tabindex="-1" 
		aria-labelledby="editPersonaLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="editPersonaModalheader">Edit persona</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body">
					<div class="p-1">
						<input v-model="persona.persona_data" type="text" class="form-control" placeholder="Data">
					</div>
					<div class="p-1">
						<select v-model="persona.persona_field_type" type="text" class="form-select">
							<option disabled>--Select field type--</option>
							<option value="text">Text</option>
							<option value="number">Number</option>
							<option value="email">Email</option>
						</select>
					</div>
					<div class="p-1">
						<label class="form-control d-flex justify-content-between">
							The data is unique <input v-model="persona.is_unique" type="checkbox">
						</label>
					</div>
					<div class="p-1">
						<label class="form-control d-flex justify-content-between">
							The data is required <input v-model="persona.is_required" type="checkbox">
						</label>
					</div>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="editPersona(persona.id)" data-bs-dismiss="modal" class="btn btn-primary">Save</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


	<!-- Delete Modals -->
	<div
		v-for="persona in personas" 
		class="modal fade" 
		:key="'modal_delete'.concat(persona.id)"
		:id="'deletePersona'.concat(persona.id)" 
		tabindex="-1" 
		aria-labelledby="deletePersonaModalLabel" 
		aria-hidden="true"
	>
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="">Delete persona</h5>
					<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
					</div>

				<div class="modal-body text-center">
					<span class="text-lowercase">{{ persona.persona_data }}</span>
				</div>
				<div class="modal-footer">
					<div class="row">
						<div class="col">
							<button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
						</div>
						<div class="col">
							<button type="button" @click="deletePersona(persona.id)" data-bs-dismiss="modal" class="btn btn-danger">Delete</button>
						</div>
					</div>
					
					
				</div>
			</div>
		</div>
	</div>


</template>

<style scoped>

	#no_test {
		width: 15rem;
	}

	.data-item {
		transition: box-shadow 100ms ease-in;
	}

	.data-item:hover {
		box-shadow: 0px 0px 5px black;
	}
</style>