<script setup>
import { onMounted, ref } from 'vue';
import { storeToRefs } from 'pinia';
import { useCandidateStore } from '@/store/candidate.store';

const candidateStore = useCandidateStore();
const {candidateSheet, currentQuestion, cursor, testCredentials } = storeToRefs(candidateStore);
const { previousQuestion, nextQuestion, changeQuestion, updateSingleChoiceAnswerStatus, handleSubmit } = candidateStore;
const remainingTime = ref(0);

const handleAnswerChange = (ev)=>{
	const answerId = ev.target.id;
	updateSingleChoiceAnswerStatus(answerId);
}

onMounted(()=>{
	const app = document.querySelector("#app");
	app.requestFullscreen();
	app.addEventListener("fullscreenschange", (ev)=>{
		const me = ev.target;
		alert(me.id);
		app.requestFullscreen();
	})
	remainingTime.value = testCredentials.value.duration*60;

	if(testCredentials.value.time_protocol == 'client'){

		const timeControl = setInterval(()=>{
			
			if(remainingTime.value != 0){
				remainingTime.value--;
			}
			else{
				clearInterval(timeControl, 1000);
			}
			
		}, 1000);
	}else {
		// code for server controlled time
	}
})

</script>

<template>
	<div class="container-fluid">
		<div class="row p-2">
			<div class="col-2">{{ remainingTime }} sec{{ remainingTime > 1 ? 's': ''}}</div>
			<div class="col">{{ testCredentials.test_name }} {{ testCredentials.duration}}</div>
			<div class="col-2">
				<button class="btn btn-primary w-100" data-bs-toggle="modal" data-bs-target="#answered_questions"><i class="fa fa-question"></i>&nbsp; <span class="hiddable-on-small">Questions</span></button>
			</div>
			<div class="col-2">
				<button class="btn btn-primary w-100" data-bs-toggle="modal" data-bs-target="#submit_modal"><i class="fa fa-upload"></i>&nbsp; <span class="hiddable-on-small">Submit</span></button>
			</div>
		</div>
		
		<div class="row">
			<div class="col-lg-6 col-sm-12 question p-4">
				{{ currentQuestion.question.data }}
			</div>
			<div class="col-lg-6 col-sm-12 answers p-2">
				<div v-if="currentQuestion.question.type == 'single_choice'">
					<div 
						v-for="answer in currentQuestion.answers"
						:key="answer.id"
						:class="['answer form-control d-flex justify-content-between mt-2 text-bold', answer.selected ? 'answer-selected' : '']"
						:id="'answer_'.concat(answer.id)"
						@click="handleAnswerChange"
					>
						<span>{{ answer.data }}</span>
					</div>
				</div>
				<div v-else>
					multiple choice
				</div>
			</div>
		</div>	
		<div class="row text-center p-2 position-fixed bottom-0 w-100">
			<div class="col">
				<button class="btn btn-primary" @click="previousQuestion"><i class="fa fa-chevron-left"></i>&nbsp;<span class="hiddable-on-small">Previous question</span></button>
			</div>
			<div class="col-8">{{cursor+1}}/{{candidateSheet.length}}</div>
			<div class="col">
				<button class="btn btn-primary" @click="nextQuestion"><span class="hiddable-on-small">Next question</span>&nbsp;<i class="fa fa-chevron-right"></i></button>
			</div>
		</div>
	</div>

	<div class="modal" id="answered_questions">
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5>Answered questions</h5>
					<button class="btn-close" data-bs-dismiss="modal"></button>
				</div>
				<div class="modal-body">
					<div 
						v-for="item, index in candidateSheet"
						:key="'qi'.concat(index)"
						:class="['question_indicator rounded-5 p-3 m-1 d-inline-block', item.question.answered == true ? 'bg-success text-light' : 'bg-light']"
						@click="changeQuestion(index)"
						style="cursor: pointer"
						data-bs-dismiss="modal"
					>
						{{ index + 1 }}
					</div>

				</div>
				<div class="modal-footer">
					<button class="btn btn-primary" data-bs-dismiss="modal">Close</button>
				</div>
			</div>
				
		</div>
	</div>

	<div class="modal" id="submit_modal">
		<div class="modal-dialog modal-dialog-centered">
			<div class="modal-content">
				<div class="modal-header">
					<h5>Are you sure you want to submit?</h5>
					<button class="btn-close" data-bs-dismiss="modal"></button>
				</div>
				
				<div class="modal-footer">
					<button class="btn btn-secondary" data-bs-dismiss="modal">No</button>
					<button class="btn btn-success" data-bs-dismiss="modal" @click="handleSubmit">Yes</button>
				</div>
			</div>
				
		</div>
	</div>
</template>

<style scoped>
/*.question, .answers {
	height: 90vh;
}

.question {

}
*/
.answer {
	cursor: pointer;
	border: 2px solid rgba(0,0,0,.3);
	transition: border 200ms ease-in;
	font-weight: bolder;
}

.answer-selected {
	border: 2px solid #0275d8;
}

@media screen and (max-width: 500px){
	.hiddable-on-small {
		display: none;
	}
}
</style>